@use '../../styles/variables' as v;
@use '../../styles/mixins' as m;

.modal {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  @include m.flexbox(0, 0, 0, center, center);
  border-radius: 0;
  max-width: fit-content;
  max-height: fit-content;
  padding: 0;
  color: v.$offWhite;
  box-shadow: 1px 1px 4px 4px rgb(2 2 2 / 64%);
  border-radius: 6px;
  overflow-y: auto;
  background-color: v.$almostBlack;
  margin: 1.75rem auto;
}

.close-button {
  background-color: v.$darkPurple; 
  opacity: 80%;
  border: none;
  color: inherit;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 2;
  cursor: pointer;
  margin: 2em;
  border-radius: 50%;
  width: 2em;
  height: 2em;
  @include m.flexbox(0, 0, 0, center, center);

  &:hover {
    transform: scale(1.5);
  }
}

.genres {
  font-size: 1.1em;
}

a {
  text-transform: none;
  padding: 0;
  text-transform: none;
  font-size: 1em;
}

a:link { text-decoration: none; }
a:visited { text-decoration: none; }
a:hover { text-decoration: none; }
a:active { text-decoration: none; }

.modal-wrapper {
  width: 50rem;
  min-height: 60vh;
  max-width: calc(100vw - 2rem);
  max-height: calc(100vh - 2rem);
}

.thumbnail {
  width: 100%;
}

.modal-bottom {
  display: flex;
  overflow-y: auto;
  padding: 2% 0;
}

.movie-info {
  background: linear-gradient(180deg, #000, transparent 80%);
  position: static;
  bottom: 0;
  right: 0;
  left: 0;
  z-index: 2;
  padding: 2% 2% 0 2%;
  background-color: #000;
}

.banner {
  opacity: 1;
}

.banner-image {
  width: 100%;
  max-height: 80vh;
}

.movie-overview {
  flex: 3;
  margin: 0 2%;
}

.movie-title {
  color: inherit;
  font-size: 1.8em;
  font-family: v.$titleFont;
}

.rating-container {
  display: flex;
}

.rating {
  padding: 0 0.5em;
  font-size: 1.5em;
}

.tagline {
  font-style: italic;
  color: v.$offWhite;
  line-height: 1.2em;
}

.modal-text {
  color: v.$offWhite;
  line-height: 1.5em;
}

.modal-title {
  color: #777;
}

.movie-details {
  @include m.flexbox(0, wrap, 0, space-between, 0);
  margin: 0 2%;
  flex: 1;
  font-size: 90%;
}

//Tablet portrait view
@media (min-width: 768px) and (max-width: 1024px) {
  .modal-bottom {
    flex-direction: column;
  }
}

//small mobile device view
@media (min-width: 320px) and (max-width: 767px) {
  .modal-bottom {
    flex-direction: column;
  }
}